*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.page {
  line-height: 1.5;
  font-family: "Dewi", sans-serif;
  color: #FFFFFF;
}
.page__body {
  background-color: #162831;
  font-size: 16px;
}

.container {
  max-width: 1298px;
  margin: 0 auto;
}

.header {
  display: grid;
  padding: 16px;
}

@media (width > 736px) {
  .header {
    grid-template-columns: max-content max-content;
    justify-content: space-between;
  }
  .header-menu {
    align-self: center;
  }
}
@media (width <= 736px) {
  .header__logo {
    z-index: 1;
  }
  .header-menu {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
  }
}
.logo {
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 8px;
  align-items: center;
}
.logo__title {
  margin: 0;
  font-weight: bold;
  font-size: 20px;
  letter-spacing: -0.06em;
}

.menu__list {
  list-style: none;
}
.menu__menu__item {
  letter-spacing: 0.02em;
  text-transform: uppercase;
}
.menu__link {
  color: #018ABE;
  text-decoration: none;
  font-size: 18px;
}
.menu__link--current {
  color: #FFFFFF;
  font-weight: bold;
  font-family: "Dewi Expanded", sans-serif;
}

@media (width <= 736px) {
  .menu {
    display: grid;
    background-color: rgba(15, 30, 37, 0.9333333333);
  }
  .menu__button {
    z-index: 1;
    display: block;
    margin-top: 24px;
    margin-right: 16px;
    padding-bottom: 20px;
    border: none;
    background-color: transparent;
    justify-self: end;
  }
  .menu__icon {
    stroke: #018ABE;
  }
  .menu__icon:hover {
    stroke: #FFFFFF;
  }
  .menu__icon-menu, .menu__button--open .menu__icon-cross {
    display: block;
  }
  .menu__icon-cross, .menu__button--open .menu__icon-menu {
    display: none;
  }
  .menu__list {
    display: none;
    z-index: 2;
    row-gap: 8px;
    justify-items: center;
    padding-top: 12px;
    padding-bottom: 24px;
    background-color: rgba(15, 30, 37, 0.9333333333);
    box-shadow: 0 50vh 0 50vh rgba(15, 30, 37, 0.9333333333);
  }
  .menu__list--open {
    display: grid;
  }
}
@media (width > 736px) {
  .menu__button {
    display: none;
  }
  .menu__list {
    display: grid;
    grid-auto-flow: column;
    column-gap: 8px;
  }
}
.header-short {
  display: grid;
  justify-items: center;
  align-items: center;
  overflow: hidden;
}
.header-short__image {
  position: relative;
  opacity: 0.6;
}

.header-info {
  position: absolute;
  z-index: 1;
  display: grid;
  justify-items: center;
  padding: 0 10px;
}
.header-info__title {
  line-height: 1.4em;
}
.header-info__descr {
  line-height: 1.4em;
  margin-bottom: 20px;
}
.header-info__button {
  padding: 9px 15px;
  border-radius: 5px;
  background-color: #018ABE;
  color: #FFFFFF;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.4em;
}

@media (width >= 320px) {
  .header-info__title {
    font-size: 16px;
    text-align: center;
  }
  .header-info__descr {
    font-size: 12px;
  }
}
@media (width > 564px) {
  .header-info__title {
    font-size: 24px;
  }
  .header-info__descr {
    font-size: 16px;
  }
}
@media (width > 879px) {
  .header-info__title {
    font-size: 38px;
  }
  .header-info__descr {
    font-size: 24px;
  }
}
@media (width <= 736px) {
  .service-card__title {
    font-weight: 600;
    font-size: 18px;
    line-height: 1em;
    text-align: center;
  }
}
.set-cards {
  display: grid;
  justify-items: center;
  gap: 15px;
  grid-auto-flow: row;
}
.set-cards__card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 250px;
  position: relative;
  min-height: 200px;
  border-radius: 5px;
  text-align: center;
}
.set-cards__card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

@media (width > 550px) {
  .set-cards {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: ". ." ". ." ". ." ". ." ". ." ". .";
  }
}
@media (width > 806px) {
  .set-cards {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas: ". . ." ". . ." ". . ." ". . ." ". . ." ". . .";
  }
}
.card__content {
  position: absolute;
  bottom: 0;
  background: rgb(0, 0, 0);
  background: rgba(0, 0, 0, 0.7);
  color: #f1f1f1;
  width: 100%;
  height: 55%;
  padding: 20px;
  overflow: scroll;
}
.card__title {
  font-size: 18px;
  border-bottom: 4px dotted #018ABE;
}
.card__description {
  font-size: 14px;
}
.card__image {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  border-radius: 5px;
}