//!-----Variables-----

$text-color: #FFFFFF;
$link-color: #018ABE;
$link-second-color: #FFFFFF;

$button-color: #FFFFFF;
$button-back-color: #018ABE;


$background-color: #162831;
$background-menu-color: #0f1e25ee;
$blocks-color: #018ABE;

$title-icon-color: #018ABE;

$control-menu-blue: #018ABE;
$control-menu-white: #FFFFFF;

//!-----Box Model-----

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

//!-----Page-----

.page {
  line-height: 1.5;
  font-family: "Dewi", sans-serif;
  color: $text-color;

  // .page__body

  &__body {
    background-color: $background-color;
    font-size: 16px;
  }
}

.container {
  max-width: 1298px;
  margin: 0 auto;
}


//!-----Header-----

.header {
  display: grid;
  padding: 16px;
}



@media (width >736px) {

  .header {
    grid-template-columns: max-content max-content;
    justify-content: space-between;
  }

  .header-menu {
    align-self: center;
  }

}

@media (width <=736px) {
  .header__logo {
    z-index: 1;
  }

  .header-menu {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
  }
}

//?---Logo

.logo {
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 8px;
  align-items: center;

  // .logo__image

  &__image {}

  // .logo__title

  &__title {
    margin: 0;
    font-weight: bold;
    font-size: 20px;
    letter-spacing: -0.06em;
  }
}


//?---Menu

.menu {

  // .menu__list

  &__list {
    list-style: none;
  }

  // .menu__item

  &__menu__item {
    letter-spacing: 0.02em;
    text-transform: uppercase;
  }

  // .menu__link

  &__link {
    color: $link-color;
    text-decoration: none;
    font-size: 18px;
  }

  // .menu__link--current

  &__link--current {
    color: $link-second-color;
    font-weight: bold;
    font-family: 'Dewi Expanded', sans-serif;
  }
}

@media(width <=736px) {
  .menu {
    display: grid;
    background-color: $background-menu-color;

    // .menu__button
    &__button {
      z-index: 1;
      display: block;
      margin-top: 24px;
      margin-right: 16px;
      padding-bottom: 20px;
      border: none;
      background-color: transparent;
      justify-self: end;
    }

    // .menu__icon

    &__icon {
      stroke: $control-menu-blue;

      &:hover {
        stroke: $control-menu-white;
      }
    }

    // .menu__icon-menu

    &__icon-menu,
    &__button--open &__icon-cross {
      display: block;
    }

    // .menu__icon-cross

    &__icon-cross,
    &__button--open &__icon-menu {
      display: none;
    }

    // .menu__list

    &__list {
      display: none;
      z-index: 2;
      row-gap: 8px;
      justify-items: center;
      padding-top: 12px;
      padding-bottom: 24px;
      background-color: $background-menu-color;
      box-shadow: 0 50vh 0 50vh $background-menu-color;
    }

    &__list--open {
      display: grid;
    }
  }
}

@media (width > 736px) {
  .menu__button {
    display: none;
  }

  .menu__list {
    display: grid;
    grid-auto-flow: column;
    column-gap: 8px;
  }

}

//!-----/header-----

//!-----header-short-----


.header-short {
  display: grid;
  justify-items: center;
  align-items: center;
  overflow: hidden;

  // .header-short__image

  &__image {
    position: relative;
    opacity: .6;
  }
}


.header-info {
  position: absolute;
  z-index: 1;
  display: grid;
  justify-items: center;
  padding: 0 10px;

  // .header-info__title

  &__title {
    line-height: 1.4em;
  }

  // .header-info__descr

  &__descr {
    line-height: 1.4em;
    margin-bottom: 20px;
  }

  // .header-info__button

  &__button {
    padding: 9px 15px;
    border-radius: 5px;
    background-color: $button-back-color;
    color: $button-color;
    font-weight: 600;
    font-size: 16px;
    line-height: 1.4em;
  }
}

@media (width >=320px) {

  .header-info__title {
    font-size: 16px;
    text-align: center;


  }

  .header-info__descr {
    font-size: 12px;
  }
}

@media (width >564px) {

  .header-info__title {
    font-size: 24px;
  }

  .header-info__descr {
    font-size: 16px;
  }
}

@media (width >879px) {

  .header-info__title {
    font-size: 38px;
  }

  .header-info__descr {
    font-size: 24px;
  }
}

//!-----/header-short-----

//!-----service-----



.service-card {




  // .service-card__title

  &__title {}

  @media (width <=736px) {

    &__title {
      font-weight: 600;
      font-size: 18px;
      line-height: 1em;
      text-align: center;
    }

  }

  // .service-card__description

  &__description {}


}

.set-cards {
  display: grid;
  justify-items: center;
  gap: 15px;
  grid-auto-flow: row;
  // .set-cards__card

  &__card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    width: 250px;
    position: relative;
    min-height: 200px;
    border-radius: 5px;
    text-align: center;

    &:hover {
      box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    }
  }
}

@media (width >550px) {

  .set-cards {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;

    grid-template-areas:
      ". ."
      ". ."
      ". ."
      ". ."
      ". ."
      ". .";
  }
}

@media (width >806px) {

  .set-cards {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas:
      ". . ."
      ". . ."
      ". . ."
      ". . ."
      ". . ."
      ". . .";
  }
}

.card {





  // .card__content

  &__content {
    position: absolute;
    bottom: 0;
    background: rgb(0, 0, 0);
    background: rgba(0, 0, 0, 0.7);
    color: #f1f1f1;
    width: 100%;
    height: 55%;
    padding: 20px;
    overflow: scroll;
  }

  // .card__title

  &__title {
    font-size: 18px;
    border-bottom: 4px dotted $blocks-color;
  }

  // .card__description

  &__description {
    font-size: 14px;
  }

  // .card__image

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    border-radius: 5px;
  }
}





//!-----/service-----